.nav__link {
  color: #000;
  cursor: pointer;
  transition: color .25s cubic-bezier(.4, 0, .2, 1);
  display: inline-flex;
  position: relative;
}

@media screen and (min-width: 768px) {
  .nav__link {
    height: auto;
    width: auto;
    padding: 0;
