.nav__link:hover, .nav__link:focus {
  color: #bbf330;
}

.nav__link:after {
  content: "";
  opacity: 0;
  border-bottom: 1px solid #bbf330;
  transition: opacity .25s cubic-bezier(.4, 0, .2, 1);
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}

.nav__link:hover:after, .nav__link:focus:after {
  opacity: 1;
}

.nav__link--modal {
  background-color: inherit;
  font-size: 16px;
  line-height: 1.1875;
  transition: background-color .25s cubic-bezier(.4, 0, .2, 1);
}

.nav__link--modal:hover, .nav__link--modal:focus {
  color: #2c2c2c;
  background-color: #bbf330;
  text-decoration: none;
}

